.layout {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: row-reverse;
    align-items: stretch;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
}

.left-layout {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 100vh;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: #fff;
}

.right-layout {
    display: flex;
    flex-direction: column;
    width: 50%;
    /*align-items: center;*/
    justify-content: center;
    background-color: #fff;
}

.right-layout-actions {
    position: relative;
    width: 100%;
}

@media only screen and (max-width: 768px) {
    .layout {
        flex-direction: column;
    }

    .left-layout {
        position: static;
        height: 35%;
        width: 100%;
    }

    .right-layout {
        border-top-left-radius: 1rem;
        border-top-right-radius: 1rem;
        height: 65%;
        width: 100%;
    }

    .right-layout-actions {
        width: 100%;
    }
}
